import {
  Box,
  Heading,
  Tab,
  TabList,
  Tabs,
  Text,
  TabPanels,
  TabPanel,
} from "@chakra-ui/react";
import React from "react";

export const Shop = () => {
  return (
    <Box
      backgroundColor={"gray.100"}
      p={16}
      display={"flex"}
      flexDirection={"column"}
      alignItems={"center"}
    >
      <Heading color={"black"} marginBottom={16}>
        Catlogo de Produtos
      </Heading>
      <Tabs variant="soft-rounded" colorScheme="red" padding={8} >
        <TabList>
          <Tab>Whey</Tab>
          <Tab>Creatina</Tab>
          <Tab>Pr-Treino</Tab>
          <Tab>Hiper Calricos</Tab>
        </TabList>
        <TabPanels>
          <TabPanel>
            <p>Whey</p>
          </TabPanel>
          <TabPanel>
            <p>Creatina</p>
          </TabPanel>
          <TabPanel>
            <p>Pr-Treino</p>
          </TabPanel>
          <TabPanel>
            <p>Hiper Calrico</p>
          </TabPanel>
        </TabPanels>
      </Tabs>
    </Box>
  );
};

export default Shop;
